import useStyles from 'isomorphic-style-loader/useStyles';
import cx from 'classnames';
import React from 'react';
import PropTypes from 'prop-types';
import s from './Home.css';

export default function Home({ news }) {
  useStyles(s);
  return (
    <div className={s.root}>
      <div className={s.container}>
        <h2 className={s.title}>
          Топ предложений по <strong>экскурсиям</strong>
        </h2>
        <div className={s.articleContainer}>
          {news.tours
            .filter(item => item.review_count > 0)
            .map(item => (
              <article key={item.title} className={s.newsItem}>
                {/* eslint-disable-next-line react/jsx-no-target-blank */}
                <a href={item.url} target="_blank">
                  <div className={s.newsCard}>
                    <div className={s.newsLabels}>
                      {item.review_count > 0 && (
                        <div className={cx(s.newsLabel, s.newsLabelRating)}>
                          <span role="img" aria-label="rating">
                            ⭐ {item.rating}
                          </span>
                        </div>
                      )}
                      {item.review_count > 0 && (
                        <div className={cx(s.newsLabel, s.newsLabelSpeech)}>
                          <span role="img" aria-label="speech">
                            💬 {item.review_count}
                          </span>
                        </div>
                      )}
                    </div>
                    <div className={s.newsImg}>
                      <img
                        src={item.photos ? item.photos[0].medium : 'foo'}
                        alt={item.title}
                        width={200}
                      />
                    </div>
                    <h3 className={s.newsTitle}>{item.title}</h3>
                    <div
                      className={s.newsDesc}
                      // eslint-disable-next-line react/no-danger
                      dangerouslySetInnerHTML={{ __html: item.tagline }}
                    />
                    <span className={s.newsPrice}>{item.price}</span>
                  </div>
                </a>
              </article>
            ))}
        </div>
      </div>
    </div>
  );
}

Home.propTypes = {
  news: PropTypes.shape({
    tours: PropTypes.arrayOf(
      PropTypes.shape({
        title: PropTypes.string.isRequired,
        link: PropTypes.string,
        content: PropTypes.string,
      }),
    ),
  }).isRequired,
};
